.container {
    width: 100%;
    min-height: 100vh;
    overflow: hidden;
    position: relative;
    margin-bottom: -20.5px;
  }
  
  .background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    z-index: -1;
  }
  
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  .content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    z-index: 1;
  }
  
  .title {
    font-size: 36px;
    color: #fff;
  }
  
  .link {
    text-decoration: none;
  }
  
  .button {
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    padding: 12px 24px;
    font-size: 18px;
    cursor: pointer;
  }
  
  .button:hover {
    background-color: #0044ff;
  }
  