.dogCard {
    display: inline-block;
    width: 75%; 
    box-sizing: border-box;
    padding: 10px;
  }
  
  .link {
    text-decoration: none;
  }
  
  .title {
    font-size: 18px;
    margin-bottom: 5px;
    color: #000000;
  }
  
  .content {
    background-color: rgba(255, 255, 255, 0.7);
    border-radius: 8px;
    padding: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.195);
    transition: transform 0.3s ease;
  }
  
  .content:hover {
    transform: translateY(-5px); /*? Aplica una animaci√≥n de desplazamiento hacia arriba al pasar el mouse por encima */
  }
  
  .temperaments {
    font-size: 14px;
    margin-bottom: 5px;
  }
  
  .info {
    font-size: 14px;
    margin-bottom: 5px;
  }
  
  .imageContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
  }
  
  .image {
    max-width: 100%;
    max-height: 100%;
    border-radius: 8px;
  }
  
  .confirmationMessage {
    color: green;
    font-weight: bold;
  }
  